body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ui.menu.top-menu {
  border: none;
  /*remove semantic's border*/ }
  .ui.menu.top-menu .logo {
    width: 17rem; }
  .ui.menu.top-menu .nav-container {
    justify-content: space-between;
    /*maximize whitespace between elements*/
    flex-grow: 1;
    /*By setting flex-grow to 1, we force the nav-container to span the entire available width that is not already covered by the logo element. */
    padding: 0; }
  .ui.menu.top-menu .search-input {
    padding-left: 0;
    width: 33%; }
    .ui.menu.top-menu .search-input form {
      width: 100%; }
  .ui.menu.top-menu .header-icon {
    color: #a0a0a0; }

.sidebar-item {
  background: #f5f5f5; }
  .sidebar-item span i.icon {
    margin-right: 20px;
    color: #888888; }

.highlight-item span {
  font-weight: 600; }

.highlight-item i.icon {
  color: #ff0002; }

.sidebar-item-alignment-container {
  display: flex;
  align-items: center; }

.side-bar-header {
  color: #6d6d6d; }

.subscription {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .subscription i.icon {
    color: #ff0002; }
  .subscription .new-videos-count {
    color: #888888; }

.footer-block {
  padding-bottom: 10px;
  padding-left: 16px;
  color: #918888; }

.ui.menu.fixed.side-nav {
  background-color: #f5f5f5;
  margin-top: 64px;
  overflow-y: auto;
  padding-bottom: 64px; }
  .ui.menu.fixed.side-nav .sidebar-item:hover {
    background: #ebebeb; }

.side-nav.ui.vertical.menu {
  width: 17rem; }

.video-info {
  color: #111111; }
  .video-info .video-preview-metadata-container {
    padding-top: 5px;
    font-size: 13px;
    color: #6E6E6E; }
    .video-info .video-preview-metadata-container .channel-title {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis; }
  .video-info .show-max-two-lines {
    overflow: hidden;
    line-height: 1.4em;
    max-height: 2.8em; }
  .video-info .semi-bold {
    font-weight: 600; }

.video-preview {
  display: grid;
  grid: 118px auto / 210px; }

.image-container {
  position: relative;
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  /* Video duration label at bottom right */ }
  .image-container .time-label {
    position: absolute;
    background: #111111;
    bottom: 0;
    right: 0;
    opacity: 0.8;
    border-radius: 2px;
    font-weight: 500;
    color: white;
    margin: 4px;
    padding: 2px 4px;
    line-height: 12px; }

.home {
  margin-top: 64px;
  margin-left: 17rem; }

